html,
body {
    background: #111111;
    color: #bec7d2;
    font-family: 'Roboto', Helvetica, Arial, sans-serif;
    font-weight: normal;
    font-size: 0.92rem;
    line-height: 1.22rem;
    padding: 0;
    margin: 0;
    height: 100%;
    text-shadow: 1px 1px 0 rgba(0, 0, 0, 0.3);
}

body {
    overflow-y: scroll;
}

.always_scrollbar {
    overflow-y: scroll;
}

.container {
    width: 976px;
    margin: 0 auto;
    padding: 8px 0 16px 16px;
    overflow: auto;
}

.container2 {
    width: 100%;
    min-width: 992px;
    margin: 0 auto;
    overflow: hidden;
    padding-bottom: 16px;
}

header {
    background: url("//c15111086.r86.cf2.rackcdn.com/texture_bar2.jpg") top center;
    border-bottom: 1px #424242 dotted;
    box-shadow: 1px 1px 2px rgba(0, 0, 0, 0.6);
    padding-top: 8px;
}

footer {
    background: url("assets/texture_bar_footer2.jpg") top center;
    border-top: 1px #424242 dotted;
    box-shadow: 1px 1px 2px rgba(0, 0, 0, 0.6);
    color: #808080;
    font-size: 85%;
    line-height: 85%;
    text-align: center;
    width: 100%;
    position: relative;
    margin-top: -29px;
    /* negative height minus border */
    height: 28px;
    clear: both;
}

footer .left {
    margin-left: 8px;
}

footer .right {
    margin-right: 8px;
}

footer .container {
    padding: 8px 0;
}

#wrap {
    min-height: 100%;
}

main {
    padding-bottom: 28px;
}

#logo {
    background: url("//c15111086.r86.cf2.rackcdn.com/srl_350px.png") no-repeat;
    display: block;
    overflow: hidden;
    width: 350px;
    height: 50px;
    margin-bottom: 13px;
    float: left;
}

#logo span {
    display: none;
}

#headerright {
    display: block;
    overflow: hidden;
    height: 50px;
    margin-bottom: 13px;
    width: 594px;
    margin-right: 16px;
    float: right;
    text-align: center;
    font-family: 'Roboto', Helvetica, Arial, sans-serif;
    font-weight: 800;
    font-size: 1.21em;
    line-height: 18px;
    color: #70e36e;
}

#headerright span {
    color: #5d97ff;
}

#headerright a {
    display: block;
    padding-top: 10px;
}

#headerright a:hover {
    color: #b4ffae;
}

#headerright a:hover span {
    color: #78e0ff;
}

nav ul {
    margin: 0;
    padding: 0;
    text-indent: 0;
    clear: both;
}

nav ul li {
    list-style-type: none;
    margin: 0 0 16px 0;
    padding: 0;
}

nav ul li a {
    background-color: #424242;
    background-image: linear-gradient(to bottom, #424242, #303030);
    border-width: 1px 0 1px 1px;
    border-style: solid;
    border-color: #4c4c4c #000000 #000000 #474747;
    text-align: center;
    display: block;
    font-weight: 700;
    color: #bec7d2;
    float: left;
    box-shadow: 1px 1px 2px rgba(0, 0, 0, 0.6);
    border-radius: 5px;
    user-select: none;
    cursor: pointer;
    width: 105px;
    padding: 4px 0;
    margin: 0 16px 0 0;
}

nav ul li a:hover {
    background-color: #3c70df;
    background-image: linear-gradient(to bottom, #3c70df, #235cd4);
    border-width: 1px 0 1px 1px;
    border-style: solid;
    border-color: #4e7de2 #102a63 #102a63 #4576e0;
    color: #ffffff;
}

nav ul li a:active {
    background-color: #1448b3;
    background-image: linear-gradient(to bottom, #1448b3, #103b93);
    box-shadow: 0 0 0 rgba(0, 0, 0, 0.6);
    border-width: 1px 0 1px 1px;
    border-style: solid;
    border-color: #164fc5 #071a40 #071a40 #154cbc;
    color: #ffffff;
}

::selection {
    background: rgba(0, 0, 0, 0.6);
    color: #f7e279;
}

strong {
    color: #f7e279;
    font-weight: 700;
}

h1 {
    font-family: 'Roboto', Helvetica, Arial, sans-serif;
    font-weight: bold;
    font-size: 1.50rem;
    line-height: 1.15rem;
    text-shadow: 1px 1px 0 rgba(0, 0, 0, 0.5);
    color: #e09456;
    margin: 8px 0 8px 0;
    padding: 8px 0;
    word-wrap: break-word;
    overflow: hidden;
}

h1 img {
    position: absolute;
    margin: 0 8px;
}

h1 a {
    text-decoration: underline;
}

h2 {
    font-family: 'Roboto', Helvetica, Arial, sans-serif;
    font-weight: bold;
    font-size: 1.06rem;
    line-height: 1.15rem;
    text-shadow: 1px 1px 0 rgba(0, 0, 0, 0.5);
    color: #e09456;
    margin: 8px 0 0 0;
    padding: 8px 0;
    word-wrap: break-word;
    overflow: hidden;
}

h4 {
    display: block;
    margin: 8px 0;
    color: #f7e279;
    font-weight: 700;
}

a {
    color: inherit;
    text-decoration: none;
}

a:hover {
    color: #ffffff;
}

p {
    margin: 0;
    padding: 0 0 11.2px;
    line-height: 1.35rem;
}

ul {
    margin-left: 0;
    padding-left: 1em;
    text-indent: -1em;
    margin-top: 2px;
    margin-bottom: 2px;
}

ul li {
    list-style-type: square;
    list-style-position: inside;
}

li {
    line-height: 1.35rem;
    padding-bottom: 11.2px;
}

.noselect {
    user-select: none;
}

table {
    width: 100%;
    border-spacing: 0;
    border-collapse: collapse;
    padding: 0;
}

td {
    border: 0;
